import { createOrder, createErrorOrder } from '@/api/order'

export default {
  namespaced: true,
  state: () => ({}),
  mutations: {},
  action: {
    createErrorOrder () {
      return new Promise((resolve, reject) => {
        createErrorOrder({
        })
          .then(data => {
            console.log(data)
            resolve()
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    createOrder () {
      return new Promise((resolve, reject) => {
        createOrder({
        })
          .then(data => {
            console.log(data)
            resolve()
          })
          .catch(err => {
            reject(err)
          })
      })
    }
  }
}
